.jobs {
  background: $color-primary;
  color: #fff;
  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }  
  &__single {
    flex: 0 0 100%;
    max-width: 100%;
    padding-bottom: 3rem;
  }
  &__list {
    order: -1;
    flex: 0 0 100%;
    max-width: 100%;
  }
  &__nav {
    padding: 0;
    list-style: none;
    margin: 0;    
    display: flex;
    overflow-x: scroll;
  }
  &__title-label {
    color: $color-secondary;    
  }
  
  &__tab {
    display: none;
    &-button {
      display: block;
      color: #fff;
      &:hover,
      &:focus {
        color: #fff;
        text-decoration: none;
      }
    }
    &.active {
      display: block;
    }
  }
  &__image {
    margin-bottom: 4rem;
  }
  &__item {
    flex: 0 0 100%;
    max-width: 100%;
    &--nav {
      padding: 4rem 1.25rem 0;
    }
    &-number {
      font-size: 1.3rem;
      padding: 0.5rem 0.7rem;
      font-weight: 700;
      font-family: $font-family-heading;
      background: #fff;
      color: $color-primary;
      display: inline-block;
      margin-bottom: 2rem;
    }
    .active > & {
      &-number {
        font-weight: 700;
        font-family: $font-family-heading;
        background: $color-secondary;
      }
    }
    @include min-width('sm') {
      flex: 0 0 50%;
      max-width: 50%;
    }
    @include min-width('md') {
      flex: 0 0 25%;
      max-width: 25%;
    }
  }
  
  &__data {
    padding: 0;
    margin: 0;
    list-style: none;    
    &-item {
      display: flex;
      margin-bottom: 1rem;      
    }
    &-icon {
      flex: 0 0 2rem;
      max-width: 2rem;
      margin-right: 1.5rem;
      svg {
        * {
          fill: $color-secondary;
        }
      }
    }
    &-values {
      &--underlined {
        &:after {
          content: '';
          display: block;
          width: 75px;
          height: 2px;
          margin: 1rem 0;
          transform: translateX(-20px);
          background: #fff;
        }
      }
    }
    &-label {
      color: $color-secondary;
    }
    &-value {
      p {
        display: inline;
      }
      &--bold {
        font-weight: 700;
      }
    }
  }
  @include min-width('lg') {
    padding: 6rem 0;
    .container {
      max-width: 100%;
    }
    &__single {
      flex: 0 0 60%;
      max-width: 720px;
      padding-right: 4rem;
      padding-bottom: 0;
    }
    &__list {
      flex: 0 0 40%;
      max-width: 40%;
      flex: 0 0 40%;
      max-width: 40%;
      order: 2;
      position: relative;
    }
    &__nav {
      display: block;
      overflow: hidden;
      position: absolute !important;
      top: 0;
      left: 0;
      height: 100%;
    }
    &__item {
      max-width: 100%;
      &-number {
        font-size: 2rem;
        margin-bottom: 2rem;
      }
    }
  }
  @include min-width('xl') {
    .container {
      max-width: 1440px;
    }
    &__single,
    &__list {
      flex: 0 0 50%;
      max-width: 50%;
    }
    &__single {
      max-width: 720px;
      padding-right: 4rem;
    }
    &__data {
      font-size: 1.3rem;
    }
  }
}


