.about {
  background: $color-secondary;
  &__body {
    margin-left: -($grid-gutter-width/2);
    margin-right: -($grid-gutter-width/2);
  }
  &__content {
    background: #fff;
    padding: 4rem 1rem;
    @include min-width('md') {
      padding: 4rem;
    }
  }
  &__image {
    img {
      width: 100%;
    }
  }
  &__icons {
    display: flex;
    align-items: flex-start;
    text-align: center;
    flex-wrap: wrap;
    padding-top: 3rem;
    &-item {
      flex: 0 0 100%;
      max-width: 100%;
      padding-bottom: 1rem;
      @include min-width('sm') {
        flex: 0 0 50%;
        max-width: 50%;
      }
      @include min-width('md') {
        flex: 0 0 25%;
        max-width: 25%;
      }
    }
  }
  &__icon {
    min-height: 75px;
    &-name {
      font-weight: 700;
      font-size: 1rem;
      padding-top: 0.8em;
    }
  }
  @include min-width('lg') {
    &__body {
      margin-left: 0;
      margin-right: 0;
    }
  }
  @include min-width('xl') {
    &__body {
      display: flex;      
    }
    &__content,
    &__image {
      flex: 0 0 50%;
      max-width: 50%;
    }
    &__content {
      padding: 10rem 10rem 10rem 4rem;
      font-size: 1.3rem;
      line-height: 1.5;
    }
    &__image {
      padding: 10rem 0 0;
      transform: translateX(-6rem);
    }
  }
}


