.recruitment {
  .slick-slide {
    margin: 2rem 1rem;
  }
  &__item {
    text-align: left;
    color: #fff;
    padding-top: 50%;
    background-size: cover;
    .heading {
      color: #fff;
      padding-bottom: 0;
    }
    &-body {
      background: transparentize($color-secondary, 0.2);
      padding: 1rem 2rem;
    }
    &-excerpt {
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 100%;
      overflow: hidden;
    }
  }
  &__arrows {
    padding-top: 2rem;
  }
  &__arrow {
    background: transparent;
    border: none;
    svg {
      width: 100px;
    }
    svg * {
      fill: $color-primary;
    }
  }
  &__link-box {
    display: block;
    box-shadow: none;
    transition: all 0.3s;
    &:hover,
    &:focus {
      text-decoration: none;
      box-shadow: 0 0 20px 0 transparentize(#000,0.5);
    }
  }
}


