.button {
  background: $color-secondary;
  font-weight: 900;
  display: inline-block;
  color:#fff;
  border: none;
  padding: 0.7em 2em;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  vertical-align: middle;
  font-size: 1rem;
  text-align: center;
  transition: all 0.3s;
  margin-right: 2em;
  &:hover,
  &:focus {
    outline: none;
    background: $color-primary;
    color: $color-secondary;
    text-decoration: none;
  }
  @include min-width('lg') {
    font-size: 1.2rem;
  }
  &--as-link {
    color: $color-primary;
    background: transparent;
    font-size: 1rem;
    padding: 0.1em 0;
    border-bottom: 1px solid $color-primary;
    text-transform: none;
    &:hover,
    &:focus {
      background: transparent;
      border-color: $color-secondary;
      color: $color-secondary;
    }
  }
  &--alt-hover {
    overflow: hidden;
    position: relative;
    z-index: 1;
    &:after {
      content:'';
      display: block;
      z-index: -1;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      position: absolute;
      background: #fff;
      top:50%;
      left:50%;
      transform: translate(-50%,-50%) scale(0);
      transition: all 0.6s;
    }
    &:hover,
    &:focus {
      // background: #fff;
      // color: $color-primary;
      &:after {
        transform: translate(-50%,-50%) scale(70);
      }
    }
  }
  &--no-spacing {
    margin: 0;
  }
}


