.carousel {
  background: $color-primary;
  &__content {
    background: #fff;
    margin-top: 50%;
    padding:1.5rem;
    font-size: 1.5rem;
    border-radius: 0 4rem 0 0;
    @include min-width('lg') {
      max-width: 50%;
      padding: 5rem 3rem;
      margin-top: 30%;
    }
  }
  &__item {
    background-position: top right;
    background-repeat: no-repeat;
    background-size: contain;
    @include min-width('lg') {
      background-size: auto;
    }
  }
  &__arrows {
    display: flex;
    justify-content: space-between;
    width: 30%;
    .carousel-control-prev,
    .carousel-control-next {
      position: static;
      opacity: 1;
      width: auto;
    }
  }
  &__arrow {
    flex: 0 0 45%;
    &-icon {
      svg * {
        fill: $color-primary;
      }
    }
  }
  &__buttons {
    padding: 2rem 0;
  }  
  .carousel-indicators {
    position: relative;
    margin: 5rem 0 0;
    padding: 0 0 3rem;
    li {
      text-indent: 0;
      border: none;
      width: auto;
      height: auto;
      margin: 0.5rem;
      background: transparent;
    }
    @include min-width('lg') {
      position: absolute;
      flex-wrap: wrap;
      left: auto;
      margin: 0 10% 4rem 0;
      padding: 0;
      max-width: 40%;
    }
  }
  &__indicator {    
    &:after {
      content:'';
      display: block;
      background: transparent;
      border: 1px solid #fff;
      width: 20px;
      height: 20px;
      border-radius: 50%;
    }
    &.active:after {
      background: #fff;
    }
    &.active &-number {
      visibility: visible;
    }
    &-number {
      color: #fff;
      font-weight: 900;
      font-family: $font-family-heading;
      font-size: 5rem;
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
      line-height: 0.7;
      visibility: hidden;
      &:after,
      &:before {
        content: '';
        display: block;
        background: $color-primary;
        position: absolute;
        left: 0;
      }
      &:before {
        height: 100%;
        width: 10px;
        top:0;
      }
      &:after {
        width: 100%;
        height: 17px;
        bottom: 0;
      }
    }
  }
}


