.footer {
  background: $color-primary;
  color: #fff;
  padding: 4rem 0 0;
  .container {
    max-width: 1600px;
  }
  &__body {
    display: flex;
    flex-wrap: wrap;
  }
  &__logo {
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
  }
  &__widget {
    text-align: center;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 1rem;    
    .menu {
      list-style: none;
      padding:0;
      margin:0;
      &-item {
        margin-bottom: 1rem;
        a {
          color: #fff;
        }
      }
    }
  }
  
  &__socials {
    display: flex;
    justify-content: flex-end;
    a {
      color: #fff;
      flex: 0 0 2rem;
      margin: 1rem 1.5rem 0 0;
      box-shadow: none;
      transition: all 0.3s;
      &:hover,
      &:focus {
        box-shadow: 0 0 10px 0 transparentize(#000,0.5);
        svg * {
          fill: $color-secondary;
        }
      }
    }
    svg {
      * {
        fill: #fff;
        transition: all 0.3s;
      }
    }
  }
  &__copyright {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 1rem;
    padding-top: 2rem;
    font-size: 0.9rem;
    img {
      width: 40px;
    }
    a {
      color: #fff;
      &:hover,
      &:focus {
        color: #fff;
        text-decoration: none;
      }
    }
  }
  @include min-width('sm') {
    &__logo {
      order: -1;
    }
    &__widget {
      order: -1;
      &--narrow {
        order: 1;
        flex: 0 0 50%;
        max-width: 50%;
      }
    }
  }
  @include min-width('md') {
    &__logo,
    &__widget {
      text-align: left;      
      flex: 0 0 50%;
      max-width: 50%;
      &--narrow {        
        flex: 0 0 25%;
        max-width: 25%;
      }
    }
  }
  @include min-width('lg'){
    padding: 6rem 0 0;
    &__body {
      justify-content: space-between;
    }
    &__logo {
      flex: 0 0 25%;
      margin-right: auto;     
    }  
    &__widget {
      flex: 0 0 auto;
      max-width: 40%;
      order:initial;
      &--narrow {
        flex: 0 0 15%;
        max-width: 15%;
      }
    }
  }
}


