.form {
  font-size: 1.2rem;
  @mixin focus() {
    outline: none;
    box-shadow: 0 0 10px 0 transparentize(#000, 0.4);
  }
  @mixin placeholder() {
    color: $color-primary;
    opacity: 0.6;
  }
  &__field {
    position: relative;
    margin-bottom: 1.5rem;
    label {
      @extend %sr-only;
    }
    .form__select-body,
    input,textarea {
      border: 2px solid $color-primary;
      padding: 0.7em 1em;
      width: 100%;
      &::placeholder {
        @include placeholder();
      }
      &:focus {
        @include focus();
      }
    }    
  }  
  &__select {
    position: relative;
    outline: none;
    &.is-focused &-body {
      @include focus();
    }
    &-input {
      &.is-hidden {
        @extend %sr-only;
      }
    }
    &-list {
      background: #fff;
      &--dropdown {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 2;
        padding: 0.7rem 1rem;
        border: 2px solid $color-primary;
        border-top: none;
        box-shadow: 0 3px 10px 0 transparentize(#000,0.5);
        opacity: 0;
        visibility: hidden;
        transform: translateY(10%);
        transition: all 0.3s;
        outline: none;
        &.is-active {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
          
        }
      }
    }
    &-item {
      cursor: pointer;
      &--choice{
        &.is-highlighted,
        &:hover,&:focus {
          color: $color-secondary;
        }
      }
      &--placeholder {
        @include placeholder();
      }
    }  
  }
  &__submit {
    input[type="submit"],
    button[type="submit"] {
      @extend .button;
    }
  }
  .wpcf7-form-control-wrap {
    display: block;
  }
  &__tip,
  .wpcf7-not-valid-tip {
    position: absolute;
    font-size: 0.7em;
  }
  .wpcf7-response-output,
  &__response {
    margin: 2rem 0;
    background: #cecece;
    padding: 1rem 2rem;
    border: 1px solid darken(#cecece, 20%);
    font-size: 1rem;
    text-align: center;
    color: #404040;
    &--floating {
      position: fixed;
      bottom: 0;
      right: 0;
      max-width: 300px;
      margin: 0 2rem 2rem
    }
    &--success {
      border-color: $color-success-dark;
      background: $color-success;
    }
    &--error {
      border-color: $color-error-dark;
      background: $color-error;
      color: #fff;
    }
    
  }
  .wpcf7-mail-sent-ok {
    @extend .form__response--success;
  }
  .wpcf7-validation-errors {
    @extend .form__response--error;
  }
  @include min-width('sm') {
    &__fields {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__field {
      &--inline {
        flex: 0 0 calc(50% - 0.75rem);
        max-width: calc(50% - 0.75rem);
      }
    }
  }
}


