.contact {
  .container {
    max-width: 1440px;
  }
  &__body {
    margin:0 -15px;
  }
  &__form,
  &__data {
    padding: 4rem 15px;
  }
  &__data {
    position: relative;
    z-index: 1;
    color: #fff;
    &:after {
      content: '';
      display: block;
      background: $color-secondary;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 200%;
      z-index: -1;
    }
  }
  &__info {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 1.3rem;
    padding-top: 4rem;
    word-break: break-all;
    a {
      color: #fff;
      &:hover,
      &:focus {
        text-decoration: none;
        color: $color-primary;
      }
    }
    &-item {
      margin-bottom: 2rem;
      display: flex;
      align-items: center;
    }
    &-icon {
      display: inline-block;
      height: 2.5rem;
      min-width: 2.5rem;
      margin-right: 2rem;
      svg {
        height: 100%;
        width: auto;
        * {
          fill: #fff;
        }
      }
    }
  }

  
  @include min-width('lg') {
    &__body {
      display: flex;
      flex-wrap: wrap;
      margin:0;
    }
    &__data,
    &__form {
      flex:0 0 50%;
      max-width: 50%;
      padding: 8rem 0;
    }
    &__data {
      padding-right: 15%;
    }
    &__info {
      word-break: keep-all;
    }
    &__form {
      padding-left: 7.5%;
    }    
  }
  @include min-width('xl') {
    &__info {
      font-size: 1.8rem;
    }
  }
}


