.nav {
  &__toggle {
    background: transparent;
    border: none;
    &-bar {
      display: block;
      margin: 0.5rem 0;
      background: #fff;
      height: 5px;
      border-radius: 4px;
      width: 55px;      
      transition: all 0.3s;
    }
    &--open {
      &:hover,&:focus {
        outline: none;
        .nav__toggle-bar {
          &:nth-child(1) {
            transform: translateX(-10px);
          }
          &:nth-child(2) {
            transform: translateX(10px);
          }
        }
      }
    }
    &--close {
      width: 55px;
      height: 55px;
      position: relative;
      .nav__toggle-bar {
        position: absolute;
        top: 35%;
        left: 0;
        background: $color-primary;
        &:nth-child(1) {
          transform: rotate(-45deg);
        }
        &:nth-child(2) {
          transform: rotate(45deg);
        }
      }
    }
  }
  &__wrap {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    z-index: 300;
    background: #fff;
    padding: 4rem 1rem;
    box-shadow: -5px 0 5px 0 transparentize(#000,0.8);
    transform: translateX(110%);
    transition: all 0.3s;
    max-width: 270px;
    width: 100%;
    overflow: auto;
    &.is-open {
      transform: translateX(0);
      .nav__menu {
        opacity: 1;
      }
    }
  }
  &__close {
    text-align: right;
  }
  &__menu {
    list-style: none;
    padding: 0 0 0 3rem;
    opacity: 0;
    transition: all 0.3s;
    transition-delay: 0.15s;
    margin: 0;
    
  }
  &__item {
    text-align: right;
    font-size: 2rem;
    padding: 1em 0;
  }
  &__link {
    color: $color-primary;
  }
}


