html {
  font-size: 16px;
}
body {
  color: $color-primary;
}
%sr-only,
.sr-only {
  position: absolute;
  width: 1px !important;
  height: 1px !important;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
a {
  transition: all 0.3s;
}
p {
  margin-bottom: 1em;
}
svg {
  width: 100%;
  height: auto;
  display: inline-block;
}
img {
  max-width: 100%;
  height: auto;
  display: inline-block;
}
button {
  cursor: pointer;
  [disabled],
  [disabled="true"] {
    cursor: not-allowed;
  }
}
.container {
  max-width: 900px;
  @include min-width('xl') {
    max-width: 1600px;
  }
}
.iframe-wrap {
  width: 100%;
  padding-bottom: 56.5%;
  position: relative;
  iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.hide-desktop {
  @include min-width('xl') {
    display: none !important;
  }
}


