.cta {
  background: $color-primary;
  text-align: center;
  padding: 4rem 0 2rem;
  position: relative;
  z-index: 1;
  overflow: hidden;
  .container {
    max-width: 1200px;
  }
  .heading,& {
    color: #fff;
  }
  &__ornament {
    display: none;    
    @include min-width('lg') {
      display: block;
      background: $color-secondary;
      border-radius: 25%;
      width: 50%;
      height: 100%;
      position: absolute;
      top:0;
      z-index: -1;
      &--left {
        padding-top: 50%;
        left: 0;
        top: 50%;
        border-radius: 30%;
        height: 0;
        transform: translate(-80%,-50%) rotate(-45deg);
      }
      &--right {
        right: 0;
        border-radius: 0 0 35% 0/25%;
        transform: translate(45%,-50%) rotate(60deg);
      }
    }
  }
  &__button {
    padding-top: 3rem;
  }
  @include min-width('lg') {
    padding: 8rem 0 4rem;
    font-size: 2rem;
  }
}


