.heading {
  font-weight: 700;
  font-size: 2rem;
  font-family: $font-family-heading;
  padding-bottom: 1em;
  line-height: 1;
  h1,h2,h3,h3,h4,h5,h6 {
    font-weight: inherit;
    font-size: inherit;
    margin: 0;
  }
  @include min-width('md') {
    font-size: 3rem;
  }
  &--carousel {
    color: $color-secondary;
    
    padding-bottom: 0.6em;
    @include min-width('lg') {
      font-size: 5rem;
    }
  }
  &--big {
    @include min-width('xl') {
      font-size: 4.5rem;
    }
  }
  &--about {
    color: $color-secondary;
  }
  &--small {
    font-size: 2rem;
  }
  &--section {
    padding-bottom: 0;
  }
  &--underline {
    &:after {
      content: '';
      display: block;
      width: 75px;
      height: 2px;
      margin: 1rem 0;
      transform: translateX(-20px);
      background: #fff;
    }
  }
  &--alt {
    padding-bottom: 0;
    &:after {
      background: $color-secondary;
    }
  }
  &--job {
    font-size: 1.7rem;
    padding-bottom: 1.5rem;
    @include min-width('xl') {
      font-size: 2.25rem;
    }
  }
  &--widget {
    font-size: 1.2rem;
  }
}


