.icons {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding:0;
  margin: 0;
  text-align: left;
  &__item {
    padding: 2.5rem;
    flex: 0 0 100%;
    max-width: 100%;
    transition: all 0.3s;
    box-shadow: none;
    &-icon {
      min-height: 82px;
      display: inline-flex;
      align-items: center;
    }
    &:hover,
    &:focus {
      transform: translateY(-10px);
      box-shadow: 0 0 10px 0 transparentize(#000, 0.6);
    }
  }
  @include min-width('sm') {
    justify-content: center;
    &__item {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
  @include min-width('lg') {    
    &__item {
      flex: 0 0 33.333%;
      max-width: 33.333%;      
    }
  }
  @include min-width('xl') {
    &__item {
      flex: 0 0 20%;
      max-width: 20%;      
    }
  }
  
}


